import React from "react";

export const Footer = () => {
  let footerStyle = {
    position: "fixed",
    left: "0",
    bottom: "0",
    width: "100%",
    backgroundColor: "grey",
    color: "white",
    textAlign: "center",
    height: "50px"
  };

  return (
    <>
      <div style={footerStyle}>
        <p style={{ marginTop: "12px" }}>&copy;MonikaKaFooter</p>
      </div>
    </>
  );
};
