import React from "react";
import { Link } from "react-router-dom";

export const Header = () => {
  let headerStyle = {
    backgroundColor: "skyblue",
    width: "100%",
    height: "50px",
    margin: "auto"
  };

  return (
    <>
      <div style={headerStyle}>
        <nav className="Navigation">
          <Link style={{ padding: "2px" }} to="/">
            Home
          </Link>
          <Link style={{ padding: "2px" }} to="/About">
            About
          </Link>
        </nav>
      </div>
    </>
  );
};
